:root {
  --font-size-base: clamp(0.7rem, 1vw, 1rem);
}
.song-card {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 0;
  padding: 0;
  font-size: var(--font-size-base);
  position: relative;
}
.song-card-content {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 100%;
  color: #ABA8A7;
  padding: 0;
  margin-bottom: 0.5rem;
}
.song-card-image {
  height: 2rem;
  width: 2rem;
  margin-right: 1rem;
  object-fit: cover;
}
.song-card-info {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0;
  margin: 0;
  font-size: var(--font-size-base);
}
.song-card-title {
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-weight: 600;
}
.song-card-album,
.song-card-artist,
.song-card-genre,
.song-card-dateListened {
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.song-card-genre {
  color: #666;
}
.song-card-dividing-line {
  border-bottom: 1px solid #833b38;
  position: relative;
  width: calc(100% + 1.5rem);
  margin: 0;
  margin-left: -1.5rem;
  margin-top: 0.5rem;
}
.song-card-grid {
  display: grid;
  grid-template-columns: 1.25fr 1.25fr 1.25fr 1fr auto;
  width: 100%;
  align-items: center;
  gap: 0.5rem;
}
.song-card-dateListened {
  grid-column: 5;
  justify-self: right;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #666;
}
.song-card-modify-button {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.875rem;
  transition: background-color 0.3s ease;
  display: none;
}
.song-card:hover .song-card-modify-button {
  display: block;
}
.song-card-modify-button:hover {
  background-color: #D32F2F;
}
.song-card-kebab-menu {
  grid-column: 6;
  justify-self: right;
  cursor: pointer;
  font-size: 1.5rem;
  color: #ABA8A7;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.song-card-kebab-menu:hover {
  color: #D32F2F;
}
.song-card-menu-overlay {
  position: absolute;
  right: 10;
  border: 1px solid #ABA8A7;
  padding: 0.5rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 10;
  display: none;
  display: flex;
  gap: 0.5rem;
  top: -12%;
}
.song-card-menu-overlay.active {
  display: flex;
}
.song-card-menu-button {
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: #B51811;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.875rem;
  text-align: center;
  transition: background-color 0.3s ease;
  flex: 1;
}
.song-card-menu-button:hover {
  background-color: #D32F2F;
}
@media (max-width: 768px) {
  :root {
    --font-size-base: clamp(0.75rem, 2.5vw, 0.875rem);
  }
  .song-card {
    flex-direction: column;
    align-items: flex-start;
  }
  .song-card-content {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  .song-card-image {
    margin-bottom: 0.5rem;
    margin-right: 0;
  }
  .song-card-grid {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
  .song-card-title,
    .song-card-album,
    .song-card-artist {
    margin: 0;
    padding: 0;
  }
  .song-card-dividing-line {
    width: 100%;
    margin-left: 0;
  }
  .song-card-genre {
    display: none;
  }
}
@media (max-width: 480px) {
  :root {
    --font-size-base: clamp(0.625rem, 3vw, 0.75rem);
  }
  .song-card {
    padding: 0.5rem;
  }
  .song-card-content {
    gap: 0.5rem;
  }
  .song-card-info {
    font-size: var(--font-size-base);
  }
  .song-card-title,
    .song-card-album,
    .song-card-artist {
    margin: 0;
    padding: 0;
  }
  .song-card-dividing-line {
    margin-top: 0.25rem;
  }
  .song-card-genre {
    display: none;
  }
}
